@keyframes show {
    0% {opacity: 0;}
    100% {opacity: 1;}
}

@keyframes hide {
    0% {opacity: 1;}
    100% {opacity: 0;}
}

main {
    height: 100vh;
    width: 100vw;
    background: #efefef;
    font-size: 27px;

    .content {
        width: 500px;
        margin: 0 auto;
        padding: 20% 0;
        color: #b4b4b4;
        box-sizing: border-box;
        word-break: break-all;

        span {
            opacity: 0;
            transition: opacity 1s;
            position: absolute;
            transition: transform .5s ease;
            animation: show .5s ease .5s forwards;

            &.hidden {
                opacity: 1;
                animation: hide .5s ease .5s forwards;
            }
        }
    }
}